<template>
  <nav class="nav">
    <v-app-bar :color="$store.state.colorSet.bgColor4" app fixed class="align-center">
      <v-toolbar-title>
        <!-- <router-link to="/main/functionlist" tag="div">
          <span class="font-weight-black white--text text-caption text-sm-h6">
            {{ this.$store.state.cData.company_name }}
          </span>
          <span style="font-size: 12px; color: white">
            {{ this.$store.state.verMsg }}
          </span>
        </router-link> -->
        <router-link to="/main/functionlist" custom v-slot="{ navigate }">
          <div @click="navigate" @keypress.enter="navigate" role="link">
            <span class="font-weight-black white--text text-caption text-sm-h6">
              {{ $store.state.cData.company_name }}
            </span>
            <span style="font-size: 12px; color: white">
              {{ $store.state.verMsg }}
            </span>
          </div>
        </router-link>
      </v-toolbar-title>
      <v-spacer></v-spacer>
      <v-btn
        text
        color="white"
        @click.stop="drawer = !drawer"
        class="font-weight-black white--text text-caption text-sm-h6"
      >
        目前登入人員:{{ this.$store.state.pData.name }}
        <v-icon>mdi-apps</v-icon>
      </v-btn>
    </v-app-bar>

    <v-navigation-drawer app temporary :color="$store.state.colorSet.bgColor4" v-model="drawer" right>
      <v-list-item-title class="text-center white--text my-2"
        >功能選擇</v-list-item-title
      >
      <v-divider color="white"></v-divider>

      <v-list dense rounded nav>
        <v-list-item
          v-for="item in items"
          :key="item.label"
          :to="item.route"
          v-show="checkAuth(item.authKey)"
        >
          <v-list-item-icon>
            <v-icon class="yellow--text">{{ item.icon }}</v-icon>
          </v-list-item-icon>

          <v-list-item-content>
            <v-list-item-title class="body-1 yellow--text">{{
              item.label
            }}</v-list-item-title>
          </v-list-item-content>
        </v-list-item>

        <v-list-item @click="logout">
          <v-list-item-icon>
            <v-icon class="yellow--text">mdi-logout</v-icon>
          </v-list-item-icon>

          <v-list-item-content>
            <v-list-item-title class="body-1 yellow--text"
              >登出</v-list-item-title
            >
          </v-list-item-content>
        </v-list-item>
      </v-list>
    </v-navigation-drawer>
  </nav>
</template>

<script>
export default {
  data() {
    return {
      personnel_key: true,
      drawer: false,
      // items: [
      //   { title: '工單', icon: 'mdi-resize', route:'/main/workdesignlist' ,authKey:'workdesign_key'},
      //   { title: '出貨單', icon: 'mdi-post', route:'/main/shipmentlist' ,authKey:'shipment_key'},
      //   { title: '品名管理', icon: 'mdi-codepen', route:'/main/rawmateriallist' ,authKey:'rawmaterial_key'},
      //   { title: '沖鎖管理', icon: 'mdi-lock', route:'/main/punchlocklist' ,authKey:'punchlock_key'},
      //   { title: '折工管理', icon: 'mdi-shape-polygon-plus', route:'/main/foldlist' ,authKey:'fold_key'},
      //   { title: '加工管理', icon: 'mdi-plus-network', route:'/main/machininglist' ,authKey:'machining_key'},
      //   { title: '客戶管理', icon: 'mdi-office-building', route:'/main/customerlist',authKey:'customer_key'},
      //   { title: '人員管理', icon: 'mdi-card-account-details', route:'/main/personnellist',authKey:'personnel_key'},
      //   // { title: '登出', icon: 'mdi-receipt', route:'/login/:loginstate' },
      // ],
    };
  },
  computed: {
    items() {
      return this.$store.state.authKeys;
    },
  },
  mounted() {
    this.getSessionStorage(); //取得暫存的廠商及使用者資料
    this.checkAuth(); //判斷授權的功能
  },
  methods: {
    close(val) {
      var pop = { msg: "", type: val };
      this.$store.commit("snackbar", pop);
    },
    logout() {
      this.$store.commit("logined", false);
      this.$router.push("/login/" + sessionStorage.getItem("dName"));
      sessionStorage.clear(); //清除sessionStorage
    },
    getSessionStorage() {
      if (sessionStorage.getItem("pData") != null) {
        this.$store.commit(
          "setcData",
          JSON.parse(sessionStorage.getItem("cData"))
        );
        this.$store.commit(
          "setpData",
          JSON.parse(sessionStorage.getItem("pData"))
        );
      } else {
        this.$router.push("/login");
      }
    },
    checkAuth(authkey) {
      if (this.$store.state.pData[authkey] == "true" || authkey == "pass") {
        return true;
      }
    },
  },
};
</script>

<style lang="scss">
.v-app-bar,
.v-navigation-drawer {
  background-image: url("~@/assets/BG_top.png");
  background-repeat: repeat;
}

.v-snack__content {
  font-size: 2rem;
}
</style>